import React, { Component } from 'react';
import Header from './Component/UI/Header/Header';
import Input from './Component/UI/Input/Input';
import {ipfs,base64ArrayBuffer} from './HelperFiles/ipfs';
class App extends Component{
  
  state={
    dpHash:null,
    dpString:""
  }
 
  reader={
    onloadend:()=>{

    }
  
  }
 

 handleInputOnChange=(event)=>{
   
let file=  event.target.files[0];
let fileType=file.type;
let prefix=`data:${fileType};base64,`

let reader=new window.FileReader();
reader.readAsArrayBuffer(file);
reader.onloadend=()=>{
  console.log(reader.result);

ipfs.add(prefix +base64ArrayBuffer(Buffer(reader.result)),(error,result)=>{
  if(error){
    console.error(error);
    return;
  }

this.setState({dpHash:result});
this.getImage();
console.log(result);
});

}

 }
getImage(){
  console.log("get image")
  const req = new XMLHttpRequest();
   req.responseType = "text/html";

   req.onload = function(e) {
     var img = new Image();
     console.log("inside get image request")
     console.log(req.response)
     this.setState({dpString:req.response});
   }.bind(this);

   req.open('GET',`https://ipfs.io/ipfs/${this.state.dpHash}`, true);
   req.send();
}

  render(){
    console.log(ipfs);
    return(<div>
            <Header dpString={this.state.dpString}/>
            
            <Input onChange={this.handleInputOnChange}  />

           </div>);
  }
}


export default App;



